body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.title-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.title-bg {
    height: 100vh;
    width: 100%;
    object-fit: cover;
}

.title-txt {
    position: absolute;
    margin: auto;
}

.name {
    font-size: 60px;
}
.about-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-bg {
    height: 100vh;
    width: 100%;
    object-fit: cover;
}

.about-txt {
    position: absolute;
    margin: auto;
}

.heading {
    font-size: 60px;
}
.exp-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.exp-bg {
    height: 100vh;
    width: 100%;
    object-fit: cover;
}

.exp-text {
    position: absolute;
    margin: auto;
}

.heading {
    font-size: 80px;
}

h1 {
    justify-content: center;
    text-align: center;
}

section {
    display: flex;
    justify-content: center;
    text-align: center;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}

a:link {
    text-decoration: none;
}
